import React from 'react';
import TopBar from '../TopBar/TopBar';
import NavBar from '../NavBar/NavBar';
import HomeTab from '../HomeTab/HomeTab';
import ManageTab from '../ManageTab/ManageTab';
import ReportsTab from '../ReportsTab/ReportsTab';
import ProfileTab from '../ProfileTab/ProfileTab';

class AppHome extends React.Component {
    constructor() {
        super();
        this.state = {
            route: 'home',
            popupFormClass: 'displayNone'
        };
    }

    changeRoute = (route) => {
        this.setState({ route: route });
    }

    displayForm = () => {
       if (this.state.popupFormClass === 'displayNone') {
           this.setState({popupFormClass: 'flexCenter popupForm'});
       } else {
           this.setState({popupFormClass: 'displayNone'});
       }
    }

    render() {
        
        switch (this.state.route) {
            case 'home':
                return (
                    <div>
                        <TopBar />
                        <NavBar newRoute={this.changeRoute} />
                        <HomeTab displayForm={this.displayForm} />
                        <div className={this.state.popupFormClass}>
                            Edit Task
                        <button onClick={this.displayForm}>Back</button>
                        </div>
                    </div>
                  );

            case 'manage':
                return (
                    <div>
                        <TopBar />
                        <NavBar newRoute={this.changeRoute} />
                        <ManageTab displayForm={this.displayForm} />
                        <div className={this.state.popupFormClass}>
                            Manage Students/ Classes/ Tasks/ Semesters
                        <button onClick={this.displayForm}>Back</button>
                        </div>
                    </div>
                  );

            case 'reports':
                return (
                    <div>
                        <TopBar />
                        <NavBar newRoute={this.changeRoute} />
                        <ReportsTab displayForm={this.displayForm} />
                        <div className={this.state.popupFormClass}>
                            Reports Form
                        <button onClick={this.displayForm}>Back</button>
                        </div>
                    </div>
                  );

            case 'profile':
                return (
                    <div>
                        <TopBar />
                        <NavBar newRoute={this.changeRoute} />
                        <ProfileTab loginStatus={this.props.loginStatus} />
                        <div className={this.state.popupFormClass}>
                            Profile Popup Form
                        <button onClick={this.displayForm}>Back</button>
                        </div>
                    </div>
                  );
                  
            default: return null;
        }
    }
}

export default AppHome;